body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: #bfcae0;
  /* background-color: #B5BEC3; */
}
.container-fluid {
  padding: 0;
}

.createIntro {
  text-align: center;
  font-family: "Merriweather", serif;
  margin-top: 25px;
  color: #40676e;
}
.col-cardsets {
  align-content: center;
}

.storedQuestion,
.storedAnswer {
  font-family: "Merriweather", serif;
  border-bottom-style: solid;
}
.cardSetQ {
  width: 48%;
  float: left;
}
.cardSetA {
  width: 48%;
  margin: 0;
  float: right;
}
/* .description {
  display: inline block;
} */

.headerTerm,
.headerDefinition {
  font-size: 12px;
  color: #40676e;
  margin-top: 5px;
  font-weight: bold;
  letter-spacing: 0.5px;
  font-family: "Fjalla One", sans-serif;
}

.col-md-12 {
  padding: 0;
}
.col-sm-12 {
  padding: 0;
  width: 80%;
  margin: 0 auto;
}

.form-group {
  width: 40%;
  margin: 0 auto;
  margin-top: 7px;
}

.col-xs-8 {
  width: 350px;
  margin: 0 auto;
  margin-top: 7px;
  text-align: center;
}

.save-btn {
  margin: 0 auto;
}

.list-group {
  width: 80%;
  margin: 0 auto;
  margin-bottom: 5px;
}

.list-group-item {
  padding: 10px;
  margin-bottom: 5px;
  border-radius: 5px;
  background-color: #ffffff;
}

/* landing page */
.sbDescription {
  width: 40%;
  float: right;
  margin-right: 100px;
  margin-top: 50px;
  margin-bottom: 200px;
}

/* navbar */
nav {
  width: 100%;
}

a {
  color: #40676e;
  font-family: "Merriweather", serif;
}

.btn-primary {
  background-color: #40676e;
}

.usernameInput,
.passwordInput {
  padding-left: 10px !important;
  width: 120px;
  margin-left: 10px;
  font-family: "PT Serif", serif;
}
